import React from 'react'
import { } from 'react-bootstrap';
import { Container, Row, Col } from 'react-bootstrap';

let style = {
    background: '#6C757D'
    , textAlign: 'center'
    , color: 'white'
    , height: '50px'
    , display: 'flex'
    , alignItems: 'center'
    , justifyContent: 'center'
}

let Footer = (props) => {
    return (
        <>
            <Container fluid style={style}>
                <Row>
                    <Col>
                        &#169; 2021 ooo"Backdoor"
                    </Col>
                </Row>
            </Container>
        </>
    )

}
export default Footer